.search {
    display: flex;
    align-items: baseline;
    justify-content: center;
    height: auto;
    gap: 0.2em;

    padding-top: 0.2em;
}


.search_btn {
    position: absolute;
    top: 1vh;
    right: calc(var(--navbar-children-width) + 20px);
    font-size: 2em;
    padding: 0.2em;

    height: calc(var(--header-height) - 3vh);
}



.input {
    width: 100%;
    font-size: 1.2em;
    line-height: 2.4em;
    height: 0;
}

.input_visible {
    height: auto;
}

.search input:hover {
    box-shadow: 0 0 10px var(--accent-hover), 0 0 10px var(--accent-hover), 0 0 10px var(--accent-hover), 0 0 10px var(--accent-hover), 0 0 10px var(--accent-hover);
    border: 1px solid var(--accent);
    transition: all ease 300ms;
    background-color: var(--background-inverted);
    opacity: 1;
}

::placeholder {
    color: var(--text-secondary)
}

@media (max-width: 550px) {
    .search_btn {
        position: static;
        font-size: 1.4em;
        height: calc(var(--header-height) - 4vh);
    }

    .search{
        flex-direction: column-reverse;
        align-items: flex-end;
        padding-right: 4em;

